/* ============================================
   PROGRESS BAR
   ============================================ */
.progress-container {
  width: 100%;
  height: 10px;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 3px;
  overflow: hidden;
  margin-top: 0.375rem;
}

/* Dark theme adjustment for progress container */
.dark-theme .progress-container {
  background-color: rgba(255, 255, 255, 0.05);
}

/* Progress bar */
.progress-bar {
  height: 100%;
  background-color: var(--success-color);
  border-radius: 3px;
  transition: width 0.3s ease;
}

/* ============================================
   STATUS DISPLAY
   ============================================ */
.status-display {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.75rem;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.03);
  margin-bottom: 0.75rem;
}

/* Dark theme adjustment for status display */
.dark-theme .status-display {
  background-color: rgba(255, 255, 255, 0.03);
}

/* Status icon container */
.status-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.05);
}

/* Dark theme adjustment for status icon */
.dark-theme .status-icon {
  background-color: rgba(255, 255, 255, 0.05);
}

/* Active status icon */
.status-icon-active {
  color: var(--success-color);
