}

/* Primary status text */
.status-primary {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
}

/* Secondary status text */
.status-secondary {
  font-size: 0.875rem;
  color: var(--text-secondary);
}

/* ============================================
   PERFORMANCE METRICS
   ============================================ */
.performance-metrics-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.75rem;
  margin-bottom: 0.75rem;
}

/* Individual performance metric */
.performance-metric {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.625rem;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.03);
}

/* Dark theme adjustment for performance metric */
.dark-theme .performance-metric {
  background-color: rgba(255, 255, 255, 0.03);
}

/* Metric details container */
.metric-details {
  display: flex;
  flex-direction: column;
}

/* Metric label */
.metric-label {
  font-size: 0.75rem;
  color: var(--text-tertiary);
}

/* Metric value */
.metric-value {
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--text-primary);
}

/* ============================================
   SCENE STATUS
   ============================================ */
.scene-status-container {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0.75rem;
  border-radius: 8px;
